  }
}

.ldio-aryt2er4mkh>div {
  animation: ldio-aryt2er4mkh 1s infinite linear;
  transform-origin: 100px 100px;
}

.ldio-aryt2er4mkh>div div {
  position: absolute;
}

.ldio-aryt2er4mkh>div div:nth-child(1),
.ldio-aryt2er4mkh>div div:nth-child(2) {
  width: 164px;
  height: 164px;
  border: 24px solid;
  border-radius: 50%;
  border-color: transparent #b6ddff #b6ddff #b6ddff;
  box-sizing: border-box;
  position: absolute;
  transform: rotate(45deg);
  transform-origin: 100px 100px;
}

.ldio-aryt2er4mkh>div div:nth-child(1) {
  transform: rotate(45deg) translate(18px, 18px);
}

.ldio-aryt2er4mkh>div div:nth-child(2) {
  transform: rotate(0deg) translate(18px, 18px);
  ;
}

.ldio-aryt2er4mkh>div div:nth-child(3) {
  width: 0;
  height: 0;
  border: 24px solid;
  border-color: transparent transparent transparent #b6ddff;
  transform: translate(100px, 6px);
}

.loadingio-spinner-reload-785d5f8ast7 {
  width: 200px;
  height: 200px;
  display: inline-block;
  overflow: hidden;
  background: #FFF;
}

.ldio-aryt2er4mkh {
  width: 100%;
  height: 100%;
  position: relative;
  transform: translateZ(0) scale(1);
  backface-visibility: hidden;
  transform-origin: 0 0;
  /* see note above */
}

.ldio-aryt2er4mkh div {
  box-sizing: content-box;
}

